<template>
  <main class="user w-100">
    <div class="text col">
      <input v-model="name" class="form-control no-arrows" type="text">
    </div>
    <div class="price col">
      <input v-model.number="price" class="form-control no-arrows" type="number">
      <VueInputCalculator v-model.number="price" auto-apply class="vue-calculator" enableKeyboard>
        <i style="background-image: url('data:image/svg+xml;base64,');"></i>
      </VueInputCalculator>
    </div>
    <div class="totalPrice col">
      <input v-model.number="totalPrice" class="form-control no-arrows" disabled type="number">
    </div>
    <div class="delete text-center vertical-center cursor-pointer btn-danger col" @click="deleteUser">
      <span>x</span>
    </div>
  </main>
</template>

<script>
import VueInputCalculator from "vue-input-calculator";


export default {
  name: "DeliveryCalculatorUser",
  components: {VueInputCalculator},

  props: {
    id: Number,
    deliveryCost: Number,
    discount: Number,
    sumPrice: Number
  },

  data() {
    return {
      name: "",
      price: 0,
    };
  },
  methods: {
    deleteUser() {
      this.$emit("delete-user", this);
    },
    getPrice() {
      return this.price;
    }
  },
  watch: {
    price() {
      this.$emit("update-price", this);
    }
  },
  computed: {
    totalPrice() {
      const discount = this.discount ? this.discount : 0;
      const deliveryCost = this.deliveryCost ? this.deliveryCost : 0;
      // Стоимость заказа с учётом скидки + пропорциональная цена за доставку
      const res = this.price * ((1 - discount / 100) + deliveryCost / this.sumPrice);
      return Math.round(res * 100) / 100;
    }
  }
};
</script>

